/* Media queries */

@media screen and (min-width: 800px) {
  .news-item:has(header:empty):not(.loading) {
    justify-content: flex-end;

    & > header {
      display: block;
      width: 80%;
      height: 100%;
    }
  }
}

@media screen and (max-width: 800px) {
  body {
    max-width: 60em;
    margin-bottom: -5.5em;
    padding-top: 5.5em;

    &.scrolled {
      padding-top: 2em;
    }
  }

  #app {
    width: 100%;
  }

  .masthead {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 9999;
    width: 100vw;

    /* height: 5em; */
    margin: 0;
    background-color: var(--newsprint);
    border-bottom: 1px var(--shadows) solid;
  }

  .masthead h1 {
    /* margin: 0;
        padding: 0.6em 0; */
    font-size: 3.6em;
    border: none;
  }

  .masthead .strapline {
    display: none;
  }

  .noise {
    padding: 0.6em 0;
    border-top: none;

    & ul {
      gap: 0.4em;
    }
  }

  article.loading {
    flex-direction: column;
    width: auto;
    height: 470px;
  }

  article.loading header.loading-bg {
    width: 100%;
    height: 80%;
  }

  article.loading section.loading-bg {
    width: 100%;
  }

  .noise {
    /* width: 90%; */
  }

  .noise .pill {
    font-size: 0.8em;
  }

  .newsfeed-wrap {
    max-width: 90vw;
    height: 75vh;
    padding-bottom: 6em;
    /* overflow-y: scroll;
    overflow-x: hidden; */
  }

  .newsfeed {
    display: flex;
    flex-flow: row wrap;
    align-items: flex-start;
    max-width: 90vw;
    /* max-width: 100%; */
    min-width: min-content;
  }

  .news-content {
    max-width: inherit;
  }

  .newsfeed li {
    width: 50%;

    /* flex: 2 auto; */
  }

  .news-item {
    flex-direction: column;
    gap: 0.4rem;
    min-height: 300px;

    & header > a {
      display: block;
      aspect-ratio: auto;
    }
  }

  .news-item .details {
    align-items: flex-start;
  }

  .news-item header {
    /* padding: 0.2em; */

    /* margin-bottom: 0.4em; */
  }

  .news-item header img {
    width: 100%;
    height: auto;
  }

  .news-item .abstract {
    margin-top: -0.3em;
  }
}

@media screen and (max-width: 450px) {
  html {
    overflow: visible;
  }
  body {
    padding-top: 4em;
  }

  [data-network='offline'] .offline-notice {
    position: fixed;
  }

  [data-network='offline'] .masthead {
    top: 40px;
  }

  .masthead h1 {
    font-size: 2.6em;
  }

  .noise {
    padding: 0.6em;
    border-top: none;
  }

  .newsfeed-wrap.has-overflowY {
    width: 100%;
    max-width: initial;
    height: auto;
  }

  .newsfeed {
    flex-direction: column;
    margin: 0;
  }
  .newsfeed li {
    flex-flow: column nowrap;
    width: 100%;
  }

  .news-item .details {
    flex-direction: row;
    gap: 0.25em;
  }

  #stt.-is-revealed {
    width: 90vw;
    bottom: 5vw;
    right: 5vw;
    height: 55px;
  }
}

@media screen and (max-width: 350px) {
  .masthead h1 {
    font-size: 1.6em;
  }
}

@media (orientation: landscape) {
  .newsfeed-wrap {
    height: 60vh;
  }
}

@media screen and (min-width: 1080px) and (orientation: landscape) {
  body {
    max-width: 1080px;
  }
  .newsfeed-wrap {
    height: 70vh;
  }
  .newsfeed {
    flex-flow: wrap;
    align-items: flex-start;
    flex-direction: row;
    display: flex;

    & li {
      width: 50%;
    }
    & .news-item {
      flex-direction: column;
    }
    & .details {
      justify-content: center;
    }
    & .news-content {
      max-width: inherit;
      text-align: center;
    }
  }
}

@media screen and (prefers-reduced-motion: reduce) {
  html > * {
    transition: all 0.1s ease;
  }

  html,
  .newsfeed-wrap {
    scroll-behavior: auto;
    transition: scroll 0s;
  }
}
