/* News items */
.news-item {
  display: flex;
  flex-direction: row;
  gap: 1rem;
  margin: 0.5em;
  padding: 0.8em;
  border: 1px var(--shadows) solid;
  border-radius: 3px;
  transform: translateY(15px);
  opacity: 0;
  filter: brightness(0.97);
  transition: transform 0.4s ease, opacity 0.6s ease;

  & > * {
    transition: 0.2s filter linear;
  }

  &.loaded {
    transform: translateY(0);
    opacity: 1;
    transition-delay: 0s;
  }

  &:hover {
    box-shadow: var(--shadows) 1px 1px 5px;
    filter: brightness(1);
  }
  & header {
  }
  & header > a {
    justify-content: center;
    overflow: hidden;
    height: 100%;
    overflow: hidden;
    border-radius: 3px;
    /* aspect-ratio: 4 / 3; */
  }

  & header img {
    height: 100%;
    object-fit: cover;
    background-color: var(--newsprint-dk);
    box-shadow: var(--shadows) 2px 2px 2px;
    border-radius: 3px;
  }

  & h3 {
    margin-top: 2px;
    margin-bottom: 8px;
    font-weight: 700;
    font-size: 1.25rem;
    font-style: italic;
    line-height: 1.4375rem;
    -webkit-letter-spacing: 0.2px;
    -moz-letter-spacing: 0.2px;
    -ms-letter-spacing: 0.2px;
    letter-spacing: 0.2px;
  }

  & .details {
    display: flex;
    align-items: center;
    padding: 0 0.5em 0.5em;
    font-size: small;
    border-bottom: 1px var(--shadows) solid;
  }

  & .byline {
    display: inline-block;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
    margin-left: 0.2em;
    padding-left: 0.4em;
    overflow: hidden;
    color: var(--charcoal);
    font-style: italic;
    text-overflow: ellipsis;
    border-left: 1px var(--shadows) solid;
    width: fit-content;
    line-height: 1.4;
  }

  & .abstract {
    min-height: 3rem;
    margin: 0 0.7em;
    line-height: 1.3em;

    & p:first-of-type {
      margin-block-start: 0.5em;
    }

    & p::first-letter {
      font-size: ;
    }
  }

  & header:empty {
    /* display: none; */
  }
}

body.loading .news-item > * {
  filter: blur(3px);
}

.news-content {
  max-width: 30%;
}

.news-item a.read-more,
.news-item h3 a {
  color: var(--text-black);
  text-decoration: none;
  transition: color 0.6s ease;
  -webkit-font-smoothing: antialiased;
}

.news-item a.read-more {
  font-size: small;
  font-style: italic;
  text-decoration: underline;
}

.news-item a.read-more:hover,
.news-item h3 a:hover {
  color: var(--charcoal);
  text-decoration: none;
}
