#stt {
  position: fixed;
  bottom: 3em;
  right: calc(50vw - 12px);
  display: block;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 0;
  height: 0;
  overflow: none;
  color: inherit;
  text-decoration: none;
  border: none;
  border-radius: 100px;
  outline: 0;
  box-shadow: 0 3px 10px var(--shadows);
  visibility: hidden;
  cursor: grab;
  opacity: 0;
  transition: all 0.3s cubic-bezier(0.25, 0.25, 0, 1);
  -webkit-tap-highlight-color: none;

  &.-is-revealed {
    width: 40px;
    height: 40px;
    background-color: var(--t-magazine-pill, red);
    visibility: visible;
    opacity: 1;
  }
}
