/* Masthead */
.masthead {
  align-items: center;
  justify-content: center;
  margin: 0.6em 0;
  transition: font-size 0.1s ease, height 0.1s ease;
  position: sticky;
  top: 0;
  z-index: 1;
  background-color: var(--newsprint);

  & a {
    color: inherit;
    text-decoration: none;
  }

  & h1 {
    font-size: 2.6em;
    font-family: 'Chomsky Regular', serif;
    text-align: center;
    border-bottom: 1px var(--shadows) solid;
    margin-block-start: 0;
    margin-block-end: 0;
    transition: font-size 0.1s ease;
    line-height: 1.5;
  }

  & .strapline {
    height: auto;
    font-size: 1em;
    font-style: italic;
    line-height: 1.3em;
    text-align: center;
    opacity: 1;
    transition: height 0.1s, visibility 0.1s, opacity 0.1s, margin 0.1s;
    -webkit-animation-delay: -0.2s;
    animation-delay: -0.2s;
    margin-block-start: 0.5em;
  }
}

[data-network='offline'] {
  & .noise,
  & .masthead {
    margin-top: 40px;
  }
}

.scrolled .masthead {
  margin-bottom: 0;

  & h1 {
    font-size: 1.5em;
    border-bottom: none;
  }

  & .strapline {
    height: 0;
    opacity: 0;
    margin-block-start: 0;
  }
}
